body {
  margin-right: auto;
  margin-left: auto;
}

.App {
  align-content: center;
  align-items: center;
  font-family: sans-serif;
  text-align: center;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}

.App p {
  color: blue;
  font-family: "Times New Roman", Times, serif;
  font-size: 30px;
  font-style: bold;
}

.App button {
  padding: 5px;
  border-radius: 5px;
  margin-right: 5px;
  background-color: #4b0082;
  color: #fff;
  width: 80px;
  height: 40px;
}

.Fim {
  text-align: center;
  margin-top: 150px;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}

.Fim p {
  font-size: 25px;
}

.Fim button {
  padding: 5px;
  border-radius: 5px;
  margin-right: 5px;
  background-color: #4b0082;
  color: #fff;
  width: 130px;
  height: 40px;
}

.ButtonStart {
  padding: 5px;
  border-radius: 5px;
  margin-right: 5px;
  background-color: #4b0082;
  color: #fff;
  width: 130px;
  height: 40px;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}
